.Sierpinski-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.sierpinski-info-icon {
  position: absolute;
  top: 10px;
  right: 10px;
  z-index: 10;
}

.sierpinski-controls-container {
  display: flex;
  justify-content: space-evenly;
  align-items: flex-start;
  width: 100%;
}

.sierpinski-iterations-control,
.sierpinski-color-control {
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* .sierpinski-color-control {
    display: flex;
    flex-direction: column;
    align-items: center;
} */

.Sierpinski-input-number {
  margin-bottom: 20px;
}

.Sierpinski-triangle-viewer {
  width: 90%; /* this is an important property and should be fiddled with first */
  /* width: auto; */
  /* height: auto; */
  /* height: 70%; */
  margin: auto;
  padding: 10px;
}

.Sierpinski-triangle-viewer svg {
  width: 100%;
  height: auto;
  display: block;
  padding: 10px;
}
